body {
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-x: hidden;
  height: 100%;
  font-family: "Satoshi", sans-serif;
  font-weight: 400;
  line-height: 1.9;
  letter-spacing: 0;
  color: var(--color-white);
  background-color: var(--color-black);
}

.site-container {
  overflow: hidden;
}

.bottom {
  margin-bottom: 12.5rem;
}

.container {
