}
.circle-bg::before {
  content: "";
  position: absolute;
  z-index: -1;
  border-radius: 50%;
  width: 690px;
  height: 100%;
  background: linear-gradient(247.71deg, rgba(174, 200, 255, 0.3) 0%, rgba(5, 68, 208, 0.3) 40.26%, rgba(255, 255, 255, 0) 122.71%);
  filter: blur(130.494px);
}

